(function () {

    var element;
    // 
    var option = {
        legend: {
            data: [{name: '', icon: "rect"}, {name: '', icon: "circle"}],
            top: "10",
            left: "10",
            textStyle: {
                color: "#fff",
                fontSize: 14,
                fontFamily: ''
            },
            itemHeight: 14,
            itemWidth: 14,
            itemGap: 30
        },
        tooltip: {
            trigger: "axis",
            formatter: "{b0}<br/> : {c0}<br /> : {c2}%",
            padding:10,
            borderWidth:1,
            borderColor:"#595180",
            textStyle:{
                fontSize:14,
                fontFamily:""
            },
            backgroundColor:"rgba(19,12,79,0.8)",
            axisPointer: {
                type: "line",
                lineStyle:{
                    color:"#8da8b4"
                }
            }
        },
        grid: {
            left:60
        },
        xAxis: [
            {
                type: 'category',
                axisLabel: {
                    textStyle: {
                        color: "#8b9dd2",
                        fontSize: 12,
                        fontFamily: ''
                    },
                    margin: 10,
                    interval: 0
                },
                axisTick: {
                    length: 8,
                    lineStyle: {
                        color: "#404E79",
                        width: 2
                    }
                },
                splitLine: {
                    show: false
                },
                axisLine: {
                    lineStyle: {
                        color: "#404E79"
                    }
                }
            }
        ],
        yAxis: [
            {
                type: 'value',
                min: 0,
                max: 6000,
                interval: 1000,
                axisLabel: {
                    formatter: function (v) {
                        return v + '\n'
                    },
                    margin: 10,
                    textStyle: {
                        fontSize: 12,
                        fontFamily: '',
                        color: "#8b9dd2"
                    }
                },
                axisTick: {
                    show: false
                },
                axisLine: {
                    show: false
                },
                splitLine: {
                    lineStyle: {
                        color: '#4d5e91', //
                        type: "dashed"
                    }
                }
            },
            {
                type: 'value',
                min: 0,
                max: 60,
                interval: 10,
                axisLabel: {
                    formatter: function (v) {
                        return v + '%\n'
                    },
                    textStyle: {
                        color: "#9c9aac",
                        fontSize: 12,
                        fontFamily: ''
                    }
                },
                axisTick: {
                    show: false
                },
                axisLine: {
                    show: false
                },
                splitLine: {
                    lineStyle: {
                        color: '#4d5e91', //
                        type: "dashed"
                    }
                }
            }
        ],
        color: ["#3b7ef4", "#3dfdb7", "#3dfdb7"],
        series: [
            {
                name: '',
                type: 'bar',
                stack: '1',
                itemStyle: {
                    normal: {
                        color: "#3BA2F9"
                    }
                },
                barCategoryGap: "60%"
            },
            {
                name: '',
                type: 'bar',
                stack: '1',
                itemStyle: {
                    normal: {
                        color: "#fff",
                        opacity: 0.1
                    }
                },
                barCategoryGap: "40%"
            },

            {
                name: '',
                type: 'line',
                yAxisIndex: 1,
                smooth: true,
                symbol: 'circle',
                symbolSize: 10,
                itemStyle: {
                    normal: {
                        opacity:0
                    },
                    emphasis:{
                        opacity:1
                    }
                },
                lineStyle: {
                    normal: {
                        width: 3
                    }
                }
            }
        ]
    };

    var main = function (/* param */ p) {

        // 
        _render_init_(p);

        // 
        _render_set_(p);

    };

    var _render_init_ = function (p) {
        // 

        var e = element = document.getElementById(p.id);

        e.style.width = p.width + 'px';
        e.style.height = p.height + 'px';

    };

    var _render_set_ = function (p) {

        var myChart = echarts.init(element);

        option.series[0].data = p.data[0];
        option.xAxis[0].data = p.categories;
        option.series[1].data = p.data[1];
        option.series[2].data = p.data[2];


        // 
        myChart.setOption(option);

    };

    // 
    if (!window.render) {

        window.render = function (v) {

            window.render._index_(v);

        };

    }

    window.render._index_ = window.render['8d998cf5-8cc4-4ac9-a2d9-d8fe7051ed41'] = main;

}());
